import { Movie } from "../../types";

export const helpers = {
  getInfoData: (data: Movie) => [
    {
      label: '',
      value: data.premier
    },
    {
      label: ' ',
      value: data.actors.join(', ')
    },
    {
      label: '',
      value: data.duration
    },
    {
      label: '',
      value: data.country
    },
    {
      label: '',
      value: data.year
    },
  ]
}
